import {
  HIDE_SNACKBAR,
  SHOW_SNACKBAR,
  RESET_INVOICE_STORE,
  SET_DASHBOARD_INFO,
  SET_PRODUCTS,
  SET_INVOICE,
  HIDE_LOADING,
  SHOW_LOADING,
  SET_PRODUCTS_RATE,
} from '@/constants/constants';
import { AnyAction, RecordObject } from '@/constants/types';

interface InvoiceState {
  loading: boolean;
  snackbar: RecordObject;
  leads: RecordObject[];
  jobs: RecordObject;
  items: RecordObject[];
  products: RecordObject[];
  invoiceData: RecordObject;
  productRates: RecordObject;
}

const INITIAL_STATE: InvoiceState = {
  loading: false,
  snackbar: { show: false, title: '', content: '', color: 'red' },
  leads: [],
  jobs: {},
  items: [],
  products: [],
  invoiceData: {},
  productRates: {},
};

export const invoiceReducer = (state = INITIAL_STATE, action: AnyAction) => {
  switch (action.type) {
    case RESET_INVOICE_STORE: {
      return {
        ...INITIAL_STATE,
      };
    }
    case SHOW_SNACKBAR: {
      return {
        ...state,
        snackbar: action.payload,
      };
    }
    case HIDE_SNACKBAR: {
      return {
        ...state,
        snackbar: { show: false, title: '', content: '', color: 'red' },
      };
    }
    case SET_DASHBOARD_INFO: {
      return {
        ...state,
        leads: action.payload.leads as RecordObject[],
      };
    }
    case SET_PRODUCTS: {
      return {
        ...state,
        products: action.payload.products as RecordObject[],
      };
    }
    case SET_INVOICE: {
      return {
        ...state,
        invoiceData: action.payload.invoiceData as RecordObject,
      };
    }
    case HIDE_LOADING: {
      return {
        ...state,
        loading: false,
      };
    }
    case SHOW_LOADING: {
      return {
        ...state,
        loading: true,
      };
    }
    case SET_PRODUCTS_RATE: {
      return {
        ...state,
        productRates: { ...state.productRates, [action.payload.productRates.name]: action.payload.productRates.rate },
      };
    }
    default:
      return state;
  }
};
