import ReceiptIcon from '@mui/icons-material/Receipt';
import SaveIcon from '@mui/icons-material/Save';
import {
  Box,
  Container,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Card,
  Button,
  SelectChangeEvent,
  FormHelperText,
} from '@mui/material';
import Grid from '@mui/material/Grid';
import { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router';

import ProductForm from '@/components/invoice/ProductForm';
import DashboardLayout from '@/components/layout/DashboardLayout';
import DashboardNavbar from '@/components/navbar/DashboardNavbar';
import { Product } from '@/constants/types';
import {
  insertOrUpdateLead,
  getJobData,
  getLeadData,
  searchProductRate,
  updateProductRate,
} from '@/redux/actions/invoiceActionCreator';
import { useAppDispatch, useAppSelector } from '@/redux/store';

const Invoice = () => {
  const dispatch = useAppDispatch();
  const { invoiceData = {}, productRates = {} } = useAppSelector((state) => state.invoice);
  const [searchParams] = useSearchParams();

  const leadId = searchParams.get('leadId');
  const jobId = searchParams.get('jobId');
  const [products, setProducts] = useState<Product[]>([
    {
      id: undefined,
      name: '',
      description: '',
      quantity: '',
      rate: '',
      total: 0,
      status: 'ACTIVE',
    },
  ]);

  const [productsError, setProductsError] = useState([
    {
      id: undefined,
      name: '',
      description: '',
      quantity: '',
      rate: '',
      total: '',
      status: 'ACTIVE',
    },
  ]);

  const [productsTouched, setProductsTouched] = useState([
    {
      id: false,
      name: false,
      description: false,
      quantity: false,
      rate: false,
      total: false,
      status: false,
    },
  ]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { id, value } = e.target;
    setFormData((prev) => ({ ...prev, [id]: value }));
    setErrors((prev) => ({ ...prev, [id]: '' }));
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const { id } = e.target;
    setTouched((prev) => ({ ...prev, [id]: true }));
  };

  const handleProductBlur = (index: number, field: keyof (typeof touched.products)[0]) => {
    setTouched((prev) => {
      const updatedProducts = [...prev.products];
      updatedProducts[index] = {
        ...updatedProducts[index],
        [field]: true,
      };
      return {
        ...prev,
        products: updatedProducts,
      };
    });
  };

  const handleChanges = (id: string, e: SelectChangeEvent) => {
    const value = e.target.value as string;
    setFormData((prev) => ({ ...prev, [id]: value }));
    setErrors((prev) => ({ ...prev, [id]: '' }));
  };

  const validateForm = () => {
    let valid = true;
    const newErrors = { ...errors };

    if (!formData?.name?.trim()) {
      newErrors.name = 'Name is required';
      valid = false;
    } else {
      newErrors.name = '';
    }

    if (!formData?.mobileNumber?.trim()) {
      newErrors.mobileNumber = 'Mobile number is required';
      valid = false;
    } else if (!/^\d{10}$/.test(formData?.mobileNumber)) {
      newErrors.mobileNumber = 'Mobile number must be valid';
      valid = false;
    } else {
      newErrors.mobileNumber = '';
    }

    if (!formData?.siteName?.trim()) {
      newErrors.siteName = 'Site name is required';
      valid = false;
    } else {
      newErrors.siteName = '';
    }

    if (!formData?.siteAddress?.trim()) {
      newErrors.siteAddress = 'Site address is required';
      valid = false;
    } else {
      newErrors.siteAddress = '';
    }

    if (!formData?.paymentStatus?.trim()) {
      newErrors.paymentStatus = 'Payment status is required';
      valid = false;
    } else {
      newErrors.paymentStatus = '';
    }

    newErrors.products = formData?.products?.map((product) => {
      const productErrors = {
        id: undefined,
        name: '',
        description: '',
        quantity: '',
        rate: '',
        total: '',
        status: '',
      };

      if (!product.name.trim()) {
        productErrors.name = 'Product name is required';
        valid = false;
      }

      if (!product.quantity.toString().trim()) {
        productErrors.quantity = 'Quantity is required';
        valid = false;
      } else if (isNaN(Number(product.quantity))) {
        productErrors.quantity = 'Quantity must be a number';
        valid = false;
      }

      if (!product.rate.toString().trim()) {
        productErrors.rate = 'Rate is required';
        valid = false;
      } else if (isNaN(Number(product.rate))) {
        productErrors.rate = 'Rate must be a number';
        valid = false;
      }

      return productErrors;
    });
    setErrors(newErrors);
    return valid;
  };

  const [formData, setFormData] = useState({
    id: undefined,
    name: '',
    mobileNumber: '',
    siteName: '',
    siteAddress: '',
    paymentStatus: '',
    products: products,
  });

  const [errors, setErrors] = useState({
    id: undefined,
    name: '',
    mobileNumber: '',
    siteName: '',
    siteAddress: '',
    paymentStatus: '',
    products: productsError,
  });

  const [touched, setTouched] = useState({
    id: false,
    name: false,
    mobileNumber: false,
    siteName: false,
    siteAddress: false,
    paymentStatus: false,
    products: productsTouched,
  });

  const addProduct = () => {
    setProductsError([
      ...productsError,
      {
        id: undefined,
        name: '',
        description: '',
        quantity: '',
        rate: '',
        total: '',
        status: 'ACTIVE',
      },
    ]);
    setProductsTouched([
      ...productsTouched,
      {
        id: false,
        name: false,
        description: false,
        quantity: false,
        rate: false,
        total: false,
        status: false,
      },
    ]);
    setProducts([
      ...products,
      {
        name: '',
        description: '',
        quantity: '',
        rate: '',
        total: 0,
        status: 'ACTIVE',
      },
    ]);
  };

  const deleteProduct = (index: number) => {
    const newProducts = products.filter((_, i) => i !== index);
    setProducts(newProducts);
    setFormData((prev) => ({ ...prev, products: newProducts }));
    const newProductsError = productsError.filter((_, i) => i !== index);
    setProductsError(newProductsError);
    setErrors((prev) => ({ ...prev, products: newProductsError }));
    const newProductsTouched = productsTouched.filter((_, i) => i !== index);
    setProductsTouched(newProductsTouched);
    setTouched((prev) => ({ ...prev, products: newProductsTouched }));
  };

  const updateProduct = (index: number, updates: Partial<Product>, getOrUpdateProduct = false) => {
    if (updates.name && getOrUpdateProduct) {
      dispatch(searchProductRate(updates.name));
    } else if (updates.rate && getOrUpdateProduct && productRates[products[index].name] != updates.rate) {
      dispatch(updateProductRate({ name: products[index].name, rate: updates.rate }));
    }
    const newProducts = [...products];
    newProducts[index] = { ...newProducts[index], ...updates };
    setProducts(newProducts);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    saveInvoiceData();
  };

  const saveInvoiceData = (generatePdf: boolean = false) => {
    const updatedProductsTouched = productsTouched.map((product) => {
      return Object.fromEntries(Object.keys(product).map((key) => [key, true])) as typeof product;
    });
    const updatedTouched = Object.fromEntries(
      Object.keys(touched).map((key) => [key, key === 'products' ? updatedProductsTouched : true]),
    ) as typeof touched;
    setProductsTouched(updatedProductsTouched);
    setTouched(updatedTouched);

    if (validateForm()) {
      console.log('Generating Invoice with data', formData);
      const productJSONs = formData.products.map((product) => {
        return {
          id: product.id,
          name: product.name,
          description: product.description,
          quantity: product.quantity,
          rate: product.rate,
          total: product.total,
          status: 'ACTIVE',
        };
      });
      const submitdata = {
        id: formData.id,
        name: formData.name,
        mobileNumber: formData.mobileNumber,
        jobs: [
          {
            id: jobId ? parseInt(jobId) : undefined,
            items: productJSONs,
            status: 'ACTIVE',
            paymentStatus: formData.paymentStatus,
            name: formData.siteName,
            address: formData.siteAddress,
            jobTotal: 4000,
          },
        ],
        generatePdf,
      };
      dispatch(insertOrUpdateLead(submitdata));
    }
  };

  useEffect(() => {
    validateForm();
  }, [touched]);

  useEffect(() => {
    setFormData((prev) => ({ ...prev, products }));
  }, [products]);

  useEffect(() => {
    if (jobId) {
      dispatch(getJobData(jobId));
    } else if (leadId) {
      dispatch(getLeadData(leadId));
    }
  }, [leadId, jobId]);

  useEffect(() => {
    if (jobId && invoiceData.lead) {
      console.log('jobdata', invoiceData);
      const productJSONs = invoiceData.items.map((product: any) => {
        return {
          id: product.id,
          name: product.name,
          description: product.description,
          quantity: product.quantity,
          rate: (product.total / parseInt(product.quantity)).toString(),
          total: product.total,
          status: 'ACTIVE',
        };
      });
      setFormData({
        id: invoiceData.lead.id,
        name: invoiceData.lead.name,
        mobileNumber: invoiceData.lead.mobileNumber,
        siteName: invoiceData.name,
        siteAddress: invoiceData.address,
        paymentStatus: invoiceData.paymentStatus,
        products: productJSONs,
      });
      setProducts(productJSONs);
      setTouched((prev) => ({ ...prev }));
    } else if (leadId) {
      console.log('Leaddata', invoiceData);
      setFormData({
        id: invoiceData.id,
        name: invoiceData.name,
        mobileNumber: invoiceData.mobileNumber,
        siteName: '',
        siteAddress: '',
        paymentStatus: '',
        products: products,
      });
      setTouched((prev) => ({ ...prev }));
    }
  }, [invoiceData]);

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <Container disableGutters sx={{ mt: 4, px: 4, maxWidth: 'none !important' }}>
        <Box py={3}>
          <Typography variant="h4" component="h1" gutterBottom sx={{ mb: 4 }}>
            Invoice Details
          </Typography>

          <Grid container spacing={3}>
            <Grid size={{ xs: 12, md: 12 }} component="form" role="form" noValidate onSubmit={handleSubmit}>
              <Card sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
                  Customer Information
                </Typography>
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
                  <Grid container spacing={3}>
                    <Grid size={{ xs: 12, sm: 6, md: 4 }}>
                      <TextField
                        id="name"
                        label="Customer Name"
                        variant="outlined"
                        value={formData.name}
                        onChange={handleChange}
                        fullWidth
                        required
                        onBlur={handleBlur}
                        error={Boolean(touched.name && errors.name)}
                        helperText={touched.name && errors.name}
                      />
                    </Grid>

                    <Grid size={{ xs: 12, sm: 6, md: 4 }}>
                      <TextField
                        id="mobileNumber"
                        label="Mobile Number"
                        variant="outlined"
                        value={formData.mobileNumber}
                        onChange={handleChange}
                        fullWidth
                        required
                        onBlur={handleBlur}
                        error={Boolean(touched.mobileNumber && errors.mobileNumber)}
                        helperText={touched.mobileNumber && errors.mobileNumber}
                      />
                    </Grid>

                    <Grid size={{ xs: 12, sm: 6, md: 4 }}>
                      <TextField
                        id="siteName"
                        label="Site Name"
                        variant="outlined"
                        value={formData.siteName}
                        onChange={handleChange}
                        fullWidth
                        required
                        onBlur={handleBlur}
                        error={Boolean(touched.siteName && errors.siteName)}
                        helperText={touched.siteName && errors.siteName}
                      />
                    </Grid>

                    <Grid size={{ xs: 12, md: 8 }}>
                      <TextField
                        id="siteAddress"
                        label="Site Address"
                        variant="outlined"
                        value={formData.siteAddress}
                        onChange={handleChange}
                        fullWidth
                        required
                        rows={3}
                        onBlur={handleBlur}
                        error={Boolean(touched.siteAddress && errors.siteAddress)}
                        helperText={touched.siteAddress && errors.siteAddress}
                      />
                    </Grid>

                    <Grid size={{ xs: 12, sm: 6, md: 4 }}>
                      <FormControl fullWidth required error={Boolean(touched.paymentStatus && errors.paymentStatus)}>
                        <InputLabel id="paymentButton">Payment Status</InputLabel>
                        <Select
                          label="Payment Status"
                          labelId="paymentButton"
                          value={formData.paymentStatus}
                          id="paymentStatus"
                          onChange={(event) => handleChanges('paymentStatus', event)}
                          onBlur={handleBlur}
                        >
                          <MenuItem value="PENDING">Pending</MenuItem>
                          <MenuItem value="COMPLETED">Paid</MenuItem>
                        </Select>
                        <FormHelperText>{touched.paymentStatus && errors.paymentStatus}</FormHelperText>
                      </FormControl>
                    </Grid>
                  </Grid>
                </Box>
              </Card>

              {products.map((product, index) => (
                <ProductForm
                  key={index}
                  index={index}
                  product={product}
                  onDelete={deleteProduct}
                  onProductChange={updateProduct}
                  addProduct={addProduct}
                  handleProductBlur={handleProductBlur}
                  errors={errors.products[index] || {}}
                  touched={touched.products[index] || {}}
                  total={products.length}
                />
              ))}
              <Grid display={'flex'} justifyContent={'center'} mt={3}>
                <Button variant="contained" startIcon={<SaveIcon />} type="submit" sx={{ minWidth: 'auto', mx: 1 }}>
                  submit
                </Button>
                <Button
                  variant="contained"
                  startIcon={<ReceiptIcon />}
                  type="button"
                  onClick={() => saveInvoiceData(true)}
                  sx={{ minWidth: 'auto', mx: 1 }}
                >
                  Generate Invoice
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </DashboardLayout>
  );
};

export default Invoice;
