import { Action, Dispatch, MiddlewareAPI } from 'redux';

export const loggerMiddleWare =
  (api: MiddlewareAPI) =>
  (next: Dispatch) =>
  (action: Action): Action => {
    console.log('%c Previous State', 'color: #fcbf49; font-weight: bold;', api.getState());
    console.log(`%c Dispatching -> ${action.type}`, 'color: #f21b3f; font-weight: bold;', action);
    const result = next(action);
    console.log('%c Next State', 'color: #29bf12; font-weight: bold;', api.getState());
    return result;
  };
