import { Backdrop, CircularProgress, CssBaseline, ThemeProvider } from '@mui/material';
import React, { useEffect } from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';

import '@/App.css';
import MDSnackbar from '@/components/snackbar/Snackbar';
import { HIDE_SNACKBAR } from '@/constants/constants';
import { useAppDispatch, useAppSelector } from '@/redux/store';
import Dashboard from '@/routes/dashboard/Dashboard';
import Invoice from '@/routes/invoice/Invoice';
import Login from '@/routes/login/Login';
import SignUp from '@/routes/signup/Signup';
import theme from '@/theme';

const App = () => {
  const { snackbar = {}, loading = false } = useAppSelector((state) => state.invoice);
  const dispatch = useAppDispatch();
  const closeErrorSB = () => dispatch({ type: HIDE_SNACKBAR });
  useEffect(() => {
    if (snackbar.show) {
      const timer = setTimeout(() => {
        dispatch({ type: HIDE_SNACKBAR });
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [snackbar]);

  const handleClose = () => {};

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/signup" element={<SignUp />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/invoice-details" element={<Invoice />} />
        <Route path="*" element={<Navigate to="/dashboard" />} />
      </Routes>
      <MDSnackbar
        color={snackbar.color}
        title={snackbar.title}
        content={snackbar.content}
        isOpen={snackbar.show}
        close={closeErrorSB}
        bgWhite
      />
      <Backdrop
        sx={(theme) => ({ color: '#fff', zIndex: theme.zIndex.drawer + 1 })}
        open={loading}
        onClick={handleClose}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    </ThemeProvider>
  );
};

export default App;
