import axios from 'axios';
class HttpClient {
  constructor(authStore, dynHeaders) {
    this.authStore = authStore;
    this.dynHeaders = dynHeaders;
    this.instance = axios.create({
      timeout: 60000,
    });
  }

  setAuthStore(authStore) {
    this.authStore = authStore;
  }

  async head(url) {
    try {
      return await this.instance.head(url);
    } catch (e) {
      console.log('HEAD called failed for url:' + url + ' error:' + e);
    }
  }

  async getAsJson(url) {
    const response = await this.get(url, 'json');
    return response;
  }

  async getAsText(url) {
    const response = await this.get(url, 'text');
    return response;
  }

  async getAsBlob(url) {
    const response = await this.get(url, 'blob');
    return response;
  }

  getLastModified(response) {
    //console.log("Last modified:" + response.headers["last-modified"])
    return (
      response && (response.headers['etag'] || response.headers['last-modified'] || response.headers['Last-Modified'])
    );
  }

  async get(url, responseType) {
    return this.#getRetry(url, responseType);
  }

  async #getRetry(url, responseType, retry) {
    try {
      //const reqHeaders = { "Accept-Encoding": "gzip,deflate" };
      const reqHeaders = {};
      this.#addHeaders(reqHeaders);
      // console.log("Headers:" + JSON.stringify(reqHeaders));
      const config = {
        headers: reqHeaders,
        responseType: responseType,
      };
      const apiUrl = `${url}`;
      console.log('Invoking http GET request:' + apiUrl);
      return await this.instance.get(apiUrl, config);
    } catch (error) {
      console.log(error);
      if (!retry && (await this.#handleError(error))) {
        //retry
        return await this.#getRetry(url, responseType, true);
      } else {
        throw error;
      }
    }
  }

  async post(url, body, responseType) {
    return this.#postRetry(url, body, responseType, false);
  }

  async #postRetry(url, body, responseType, retry) {
    try {
      // console.log("Invoking http POST request:" + url);
      const reqHeaders = {};
      this.#addHeaders(reqHeaders);
      // console.log("Headers:" + JSON.stringify(reqHeaders));
      // console.log("Body:" + JSON.stringify(body));
      const config = {
        headers: reqHeaders,
        responseType: responseType || 'json',
      };
      return await this.instance.post(url, body, config);
    } catch (error) {
      console.log(error);
      if (!retry && (await this.#handleError(error))) {
        console.log('Retrying...');
        return await this.#postRetry(url, body, responseType, true);
      } else {
        throw error;
      }
    }
  }

  async formPost(url, formData) {
    return this.#formPostRetry(url, formData, false);
  }

  async #formPostRetry(url, formData, retry) {
    try {
      console.log('Invoking http multipart POST request:' + url);
      let reqHeaders = {};
      this.#addHeaders(reqHeaders);
      if (formData.headers) {
        reqHeaders = {
          ...reqHeaders,
          ...formData.headers,
        };
      }
      /*const reqHeaders = {
        'Content-Type':'multipart/form-data'
      };*/
      const config = {
        headers: reqHeaders,
      };
      return await this.instance.post(url, formData, config);
    } catch (error) {
      console.log(error);
      if (!retry && (await this.#handleError(error))) {
        return await this.#formPostRetry(url, formData, true);
      } else {
        throw error;
      }
    }
  }

  async put(url, payload, responseType) {
    return this.#putRetry(url, payload, responseType, false);
  }

  async #putRetry(url, payload, responseType, retry) {
    try {
      // console.log("Invoking http PUT request:" + url);
      const reqHeaders = {};
      this.#addHeaders(reqHeaders);
      // console.log("Headers:" + JSON.stringify(reqHeaders));
      // console.log("Body:" + JSON.stringify(body));
      const config = {
        headers: { ...reqHeaders, ...payload.headers },
        responseType: responseType || 'json',
      };
      return await this.instance.put(url, payload.body, config);
    } catch (error) {
      console.log(error);
      if (!retry && (await this.#handleError(error))) {
        // console.log("Retrying...");
        return await this.#putRetry(url, payload, responseType, true);
      } else {
        throw error;
      }
    }
  }

  async formPut(url, formData) {
    return this.#formPutRetry(url, formData, false);
  }

  async #formPutRetry(url, formData, retry) {
    try {
      // console.log("Invoking http multipart PUT request:" + url);
      let reqHeaders = {};
      this.#addPutHeaders(reqHeaders);
      if (formData?.headers) {
        reqHeaders = {
          ...reqHeaders,
          ...formData.headers,
        };
      }
      // console.log("Headers:" + JSON.stringify(reqHeaders));

      const config = {
        headers: reqHeaders,
      };
      return await this.instance.put(url, formData, config);
    } catch (error) {
      console.log(error);
      if (!retry && (await this.#handleError(error))) {
        // console.log("Retrying");
        return await this.#formPutRetry(url, formData, true);
      } else {
        throw error;
      }
    }
  }

  #addPutHeaders(reqHeaders) {
    reqHeaders['Content-Type'] = 'multipart/form-data';
  }

  #addHeaders(reqHeaders) {
    if (this.authStore) {
      const accessToken = this.authStore.accessToken();
      if (accessToken) reqHeaders['Authorization'] = accessToken;
    }
    if (this.authStore && this.authStore.userAgent) {
      reqHeaders['X-User-Agent'] = JSON.stringify(this.authStore.userAgent);
    }
    reqHeaders['Content-Type'] = 'application/json';
    if (this.dynHeaders) {
      this.dynHeaders(reqHeaders);
    }
  }

  async #handleError(error) {
    const response = error.response;
    if (response?.status === 401) {
      if (this.authStore) {
        const refreshToken = this.authStore.refreshToken();
        if (refreshToken) {
          try {
            await this.#invokeRefreshToken(refreshToken);
            return true; //retry now
          } catch (e) {
            console.log(e);
          }
        }
      }
    }
    return false;
  }

  async #invokeRefreshToken(refreshToken) {
    console.log('Invoking refresh token call...');

    const url = 'https://molly-talented-molly.ngrok-free.app/bsc/v1/auth/validate-refresh-token';
    const body = {
      refresh_token: refreshToken,
    };

    const response = await this.instance.post(url, body);
    if (response && response.data) {
      console.log('Refresh token:' + JSON.stringify(response.data));
      this.authStore.setTokens(response.data['token'], response.data['refreshToken']);
    }
  }
}

export default HttpClient;
