import type { Middleware } from '@reduxjs/toolkit';
import { configureStore } from '@reduxjs/toolkit';
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';
import { NavigateFunction } from 'react-router';
import { persistReducer, persistStore } from 'redux-persist';
import storage from 'redux-persist/lib/storage/session';

import { PersistedRootState, RecordObject } from '@/constants/types';
import { apiMiddleware } from '@/redux/middleware/apicall.middleware';
import { loggerMiddleWare } from '@/redux/middleware/logger.middleware';
import { navigationMiddleware } from '@/redux/middleware/navigation.middleware';
import rootReducer from '@/redux/reducer/rootReducer';

const extraThunkArgs: RecordObject = {};

const persistConfig = {
  key: 'root',
  storage,
  whitelist: ['auth'],
};

const persistedRootReducer = persistReducer(persistConfig, rootReducer);

export const store = configureStore({
  reducer: persistedRootReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({ serializableCheck: false, thunk: { extraArgument: extraThunkArgs } })
      .concat(navigationMiddleware as Middleware)
      .concat(loggerMiddleWare as Middleware)
      .concat(apiMiddleware as Middleware),
});

export const setupStore = (preloadedState?: PersistedRootState) => {
  return configureStore({
    reducer: persistedRootReducer,
    preloadedState,
  });
};

export const setNavigate = (navigateFn: NavigateFunction) => {
  extraThunkArgs.navigate = navigateFn;
};

export const getNavigate = (): NavigateFunction => {
  return extraThunkArgs.navigate;
};

export const persistor = persistStore(store);

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

export const useAppDispatch: () => AppDispatch = useDispatch;
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;
