import { Box, Breadcrumbs as MuiBreadcrumbs, Typography } from '@mui/material';
import Icon from '@mui/material/Icon';
import { Link } from 'react-router-dom';

interface Props {
  icon: React.ReactNode;
  title: string;
  route: string[];
  light?: boolean;
}

const Breadcrumbs: React.FC<Props> = ({ icon, title, route, light = false }) => {
  const routes = route.slice(0, -1);

  return (
    <Box mr={{ xs: 0, xl: 8 }}>
      <MuiBreadcrumbs
        sx={{
          '& .MuiBreadcrumbs-separator': {
            color: ({ palette: { grey, white } }) => (light ? white.main : grey[600]),
          },
        }}
      >
        <Link to="/">
          <Typography color={light ? 'white' : 'dark'} sx={{ lineHeight: 0, opacity: light ? 0.8 : 0.5 }}>
            <Icon>{icon}</Icon>
          </Typography>
        </Link>
        {routes.map((el: string) => (
          <Link to={`/${el}`} key={el}>
            <Typography
              color={light ? 'white' : 'dark'}
              sx={{ lineHeight: 0, fontWeight: 'regular', textTransform: 'capitalize', opacity: light ? 0.8 : 0.5 }}
            >
              {el}
            </Typography>
          </Link>
        ))}
        <Typography
          variant="button"
          fontWeight="regular"
          textTransform="capitalize"
          color={light ? 'white' : 'dark'}
          sx={{ lineHeight: 0 }}
        >
          {title.replace('-', ' ')}
        </Typography>
      </MuiBreadcrumbs>
      <Typography fontWeight="bold" textTransform="capitalize" variant="h6" color={light ? 'white' : 'dark'} noWrap>
        {title.replace('-', ' ')}
      </Typography>
    </Box>
  );
};

export default Breadcrumbs;
