import { AxiosError } from 'axios';

import { AnyAction } from '@/constants/types';

export const normalizeFailureAction = (action: AnyAction): AnyAction => {
  if (action?.response) {
    const response = action?.response;
    let _status = {
      errCode: 'ERR_UNKNOWN',
      message: action?.response?.toString(),
    };
    if (response instanceof AxiosError) {
      if (response.code === 'ECONNABORTED') {
        _status = {
          errCode: 'ERR_TIMEOUT',
          message: 'AxiosError: Timeout Error',
        };
      } else if (!response.response) {
        _status = {
          errCode: 'ERR_NETWORK',
          message: 'AxiosError: Network Error',
        };
      }
    }
    action.response = {
      toString: () => action?.response?.toString(),
      response: {
        ...response?.response,
        data: {
          ...response?.response?.data,
          _status: {
            ...response?.response?.data?._status,
            message: response?.response?.data?._status?.message || _status.message,
            errCode: response?.response?.data?._status?.errCode || _status.errCode,
          },
        },
      },
    };
  }

  return action;
};

export const downloadPdfFromBlob = (pdfBlob: string, filename: string = 'filename'): void => {
  if (pdfBlob) {
    const blob = new Blob([pdfBlob], { type: 'application/pdf' });
    const objectUrl = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = objectUrl;
    link.download = `${filename}.pdf`;
    document.body.appendChild(link);
    link?.click();
    document?.body?.removeChild(link);
    window.URL.revokeObjectURL(objectUrl);
  }
};
