import { EditNotificationsOutlined } from '@mui/icons-material';
import EditSquareIcon from '@mui/icons-material/EditSquare';
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';
import ReceiptIcon from '@mui/icons-material/Receipt';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import * as React from 'react';
import { TableVirtuoso, TableComponents } from 'react-virtuoso';

import { NAVIGATE_TO } from '@/constants/constants';
import { ColumnData, RecordObject } from '@/constants/types';
import { generatePDF } from '@/redux/actions/invoiceActionCreator';
import { useAppDispatch } from '@/redux/store';

interface Props {
  columns: ColumnData[];
  rows: RecordObject[];
}

const VirtuosoTableComponents: TableComponents<RecordObject> = {
  Scroller: React.forwardRef<HTMLDivElement>((props, ref) => <TableContainer component={Paper} {...props} ref={ref} />),
  Table: (props) => <Table {...props} sx={{ borderCollapse: 'separate', tableLayout: 'fixed' }} />,
  TableHead: React.forwardRef<HTMLTableSectionElement>((props, ref) => <TableHead {...props} ref={ref} />),
  TableRow,
  TableBody: React.forwardRef<HTMLTableSectionElement>((props, ref) => <TableBody {...props} ref={ref} />),
};

const fixedHeaderContent = (columns: ColumnData[]) => {
  return (
    <TableRow>
      {columns.map((column) => (
        <TableCell
          key={column.dataKey}
          variant="head"
          align={column.numeric || false ? 'right' : 'left'}
          style={{ width: column.width }}
          sx={{ backgroundColor: 'background.paper' }}
        >
          {column.label}
        </TableCell>
      ))}
    </TableRow>
  );
};

const RowContent: React.FC<{
  row: RecordObject;
  columns: ColumnData[];
  leadId?: number;
}> = ({ row, columns, leadId }) => {
  const dispatch = useAppDispatch();

  const handleClick = (event: React.MouseEvent<SVGSVGElement>): void => {
    dispatch(generatePDF(row.id));
  };

  const redirect = (url: string) => dispatch({ type: NAVIGATE_TO, payload: { url } });

  return (
    <React.Fragment>
      {columns.map((column) => (
        <TableCell key={column.dataKey} align={column.numeric || false ? 'right' : 'left'}>
          {column.dataKey === 'actions' ? (
            <>
              <EditSquareIcon
                onClick={() => redirect(`/invoice-details?jobId=${row.id}`)}
                sx={{ cursor: 'pointer' }}
              ></EditSquareIcon>{' '}
              <ReceiptIcon onClick={handleClick} sx={{ cursor: 'pointer' }}></ReceiptIcon>
            </>
          ) : (
            row[column.dataKey]
          )}
        </TableCell>
      ))}
    </React.Fragment>
  );
};
const ReactVirtualizedTable: React.FC<Props> = ({ rows, columns }) => {
  return (
    <Paper style={{ height: 400, width: '100%' }}>
      <TableVirtuoso
        data={rows}
        components={VirtuosoTableComponents}
        fixedHeaderContent={() => fixedHeaderContent(columns)}
        itemContent={(_index: number, row: RecordObject) => <RowContent row={row} columns={columns} />}
      />
    </Paper>
  );
};

export default ReactVirtualizedTable;
