    light: { main: string; focus: string };
    dark: { main: string; focus: string };
    black: { light: string; main: string; focus: string };
    transparent: { main: string; focus?: string };
    coloredShadows: {
      primary: string;
      secondary: string;
      info: string;
      success: string;
      warning: string;
      error: string;
      light: string;
      dark: string;
    };
