// prop-types is a library for typechecking of props

// @mui material components
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import WarningAmberRoundedIcon from '@mui/icons-material/WarningAmberRounded';
import { SnackbarContent } from '@mui/material';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Fade from '@mui/material/Fade';
import Icon from '@mui/material/Icon';
import IconButton from '@mui/material/IconButton';
import Snackbar from '@mui/material/Snackbar';
// Material Dashboard 2 React components
import Typography from '@mui/material/Typography';

interface Props {
  color?: 'primary' | 'secondary' | 'info' | 'success' | 'warning' | 'red' | 'dark' | 'light';
  title: string;
  content: string;
  isOpen: boolean;
  close: () => void;
  bgWhite: boolean;
}

const MDSnackbar: React.FC<Props> = ({
  color = 'red',
  title = 'BSC Enterprise',
  content,
  isOpen,
  close,
  bgWhite,
  ...rest
}) => {
  let titleColor;
  let dividerColor;

  if (bgWhite) {
    titleColor = color;
    dividerColor = false;
  } else if (color === 'light') {
    titleColor = 'dark';
    dividerColor = false;
  } else {
    titleColor = 'white';
    dividerColor = true;
  }

  return (
    <Snackbar
      autoHideDuration={5000}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right',
      }}
      {...rest}
      action={
        <IconButton size="small" aria-label="close" color="inherit" onClick={close}>
          <Icon fontSize="small">close</Icon>
        </IconButton>
      }
      open={isOpen}
      slots={{ transition: Fade }}
      slotProps={{
        root: {
          sx: {
            background: 'white',
            borderRadius: '0.5rem',
            boxShadow: 3,
          },
        },
      }}
    >
      <Box minWidth="21.875rem" maxWidth="100%" borderRadius="md" p={1}>
        <Box display="flex" justifyContent="space-between" alignItems="center" color="dark" p={1.5}>
          <Box display="flex" alignItems="center" lineHeight={0}>
            <WarningAmberRoundedIcon sx={{ color: color }}></WarningAmberRoundedIcon>
            <Typography variant="button" fontWeight="medium" sx={{ color: color, ml: 0.5 }} component="span">
              {title}
            </Typography>
          </Box>
          <Box display="flex" alignItems="center" lineHeight={0}>
            <CloseRoundedIcon sx={{ color: color }} onClick={close}>
              close
            </CloseRoundedIcon>
          </Box>
        </Box>
        <Divider sx={{ margin: 0 }} light={dividerColor} />
        <Box
          p={1.5}
          sx={{
            fontSize: 15,
            color: color,
          }}
        >
          {content}
        </Box>
      </Box>
    </Snackbar>
  );
};

// Typechecking props for Snackbar
export default MDSnackbar;
