import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import AddBoxRoundedIcon from '@mui/icons-material/AddBoxRounded';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import MailRoundedIcon from '@mui/icons-material/MailRounded';
import MenuOpenRoundedIcon from '@mui/icons-material/MenuOpenRounded';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';
import NotificationsRoundedIcon from '@mui/icons-material/NotificationsRounded';
import PodcastsRoundedIcon from '@mui/icons-material/PodcastsRounded';
import SettingsRoundedIcon from '@mui/icons-material/SettingsRounded';
import ShoppingCartCheckoutRoundedIcon from '@mui/icons-material/ShoppingCartCheckoutRounded';
import { Box, Button, TextField, Theme } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import Toolbar from '@mui/material/Toolbar';
import { useState, useEffect } from 'react';
import { useLocation, Link, useNavigate } from 'react-router-dom';

import Breadcrumbs from '@/components/navbar/BreadCrumbs';
import {
  navbar,
  navbarContainer,
  navbarRow,
  navbarIconButton,
  navbarMobileMenu,
} from '@/components/navbar/DashboardNavbarStyle';
import NotificationItem from '@/components/navbar/NotificationItem';
import { NAVIGATE_TO } from '@/constants/constants';
import { setAccessToken, setRefreshToken } from '@/redux/auth/auth.slice';
import { useAppDispatch } from '@/redux/store';

interface Props {
  absolute?: boolean;
  light?: boolean;
  isMini?: boolean;
  search?: string;
  onSearchChange?: (value: string) => void;
}

const DashboardNavbar: React.FC<Props> = ({
  absolute = false,
  light = false,
  isMini = false,
  search = '',
  onSearchChange,
}) => {
  const dispatch = useAppDispatch();
  const [navbarType, setNavbarType] = useState<'fixed' | 'absolute' | 'sticky' | 'static' | 'relative'>('static');
  const [navbarConfig, setNavbarConfig] = useState({
    miniSidenav: false,
    transparentNavbar: true,
    fixedNavbar: true,
    openConfigurator: false,
    darkMode: false,
  });
  const { miniSidenav, transparentNavbar, fixedNavbar, darkMode } = navbarConfig;
  const [openMenu, setOpenMenu] = useState<null | HTMLElement>(null);
  const route = useLocation().pathname.split('/').slice(1);
  const isDashboard = route[route.length - 1] === 'dashboard';

  useEffect(() => {
    if (fixedNavbar) {
      setNavbarType('sticky');
    } else {
      setNavbarType('static');
    }
    const handleTransparentNavbar = () => {
      setNavbarConfig({
        ...navbarConfig,
        transparentNavbar: (navbarConfig.fixedNavbar && window.scrollY === 0) || !navbarConfig.fixedNavbar,
      });
    };
    window.addEventListener('scroll', handleTransparentNavbar);
    handleTransparentNavbar();
    return () => window.removeEventListener('scroll', handleTransparentNavbar);
  }, [fixedNavbar]);

  const handleMiniSidenav = () => setNavbarConfig({ ...navbarConfig, miniSidenav: !navbarConfig.miniSidenav });
  const handleConfiguratorOpen = () =>
    setNavbarConfig({ ...navbarConfig, openConfigurator: !navbarConfig.openConfigurator });
  const handleOpenMenu = (event: React.MouseEvent<HTMLButtonElement>) => setOpenMenu(event.currentTarget);
  const handleCloseMenu = () => setOpenMenu(null);
  const redirect = (url: string) => dispatch({ type: NAVIGATE_TO, payload: { url } });
  const onLogout = () => {
    dispatch(setAccessToken({ token: '' }));
    dispatch(setRefreshToken({ refreshToken: '' }));
    redirect('/login');
  };

  const renderMenu = () => (
    <Menu
      anchorEl={openMenu ?? undefined}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={Boolean(openMenu)}
      onClose={handleCloseMenu}
      sx={{ mt: 2 }}
    >
      <NotificationItem icon={<MailRoundedIcon></MailRoundedIcon>} title="Check new messages" />
      <NotificationItem icon={<PodcastsRoundedIcon></PodcastsRoundedIcon>} title="Manage Podcast sessions" />
      <NotificationItem
        icon={<ShoppingCartCheckoutRoundedIcon></ShoppingCartCheckoutRoundedIcon>}
        title="Payment successfully completed"
      />
    </Menu>
  );

  const iconsStyle = ({ palette: { dark, white, textColor }, functions: { rgba } }: Theme) => ({
    color: () => {
      let colorValue = light || darkMode ? white.main : dark.main;
      if (transparentNavbar && !light) {
        colorValue = darkMode ? rgba(textColor.main, 0.6) : textColor.main;
      }
      return colorValue;
    },
  });

  return (
    <AppBar
      position={absolute ? 'absolute' : navbarType}
      color="inherit"
      sx={(theme) => navbar(theme, { transparentNavbar, absolute, light, darkMode })}
    >
      <Toolbar sx={(theme: Theme) => navbarContainer(theme)}>
        <Box color="inherit" mb={{ xs: 1, md: 0 }} sx={(theme) => navbarRow(theme, { isMini })}>
          <Breadcrumbs
            icon={<HomeRoundedIcon></HomeRoundedIcon>}
            title={route[route.length - 1]}
            route={route}
            light={light}
          />
        </Box>
        {isMini ? null : (
          <Box sx={(theme) => navbarRow(theme, { isMini })}>
            {isDashboard && onSearchChange && (
              <Box pr={1}>
                <TextField
                  label="Search Lead Name"
                  value={search}
                  onChange={(e) => onSearchChange(e.target.value)}
                  variant="outlined"
                  size="small"
                />
              </Box>
            )}
            {isDashboard && (
              <>
                <Box pr={1}>
                  <Button
                    variant="contained"
                    startIcon={<AddBoxRoundedIcon />}
                    onClick={() => redirect('/invoice-details')}
                  >
                    Create New Lead
                  </Button>
                </Box>
              </>
            )}
            <Box pr={1}>
              <Button variant="contained" onClick={onLogout} sx={{ minWidth: 'auto', bgcolor: 'green' }}>
                Logout
              </Button>
            </Box>
            {/* <Box color={light ? 'white' : 'inherit'}>
              <Link to="/authentication/sign-in/basic">
                <IconButton sx={navbarIconButton} size="small" disableRipple>
                  <AccountCircleRoundedIcon></AccountCircleRoundedIcon>
                </IconButton>
              </Link>
              <IconButton size="small" disableRipple color="inherit" sx={navbarMobileMenu} onClick={handleMiniSidenav}>
                {miniSidenav ? (
                  <MenuOpenRoundedIcon sx={iconsStyle} fontSize="medium"></MenuOpenRoundedIcon>
                ) : (
                  <MenuRoundedIcon></MenuRoundedIcon>
                )}
              </IconButton>
              <IconButton
                size="small"
                disableRipple
                color="inherit"
                sx={navbarIconButton}
                onClick={handleConfiguratorOpen}
              >
                <SettingsRoundedIcon></SettingsRoundedIcon>
              </IconButton>
              <IconButton
                size="small"
                disableRipple
                color="inherit"
                sx={navbarIconButton}
                aria-controls="notification-menu"
                aria-haspopup="true"
                onClick={handleOpenMenu}
              >
                <NotificationsRoundedIcon></NotificationsRoundedIcon>
              </IconButton>
              {renderMenu()}
            </Box> */}
          </Box>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default DashboardNavbar;
