import { Box, Container, TextField } from '@mui/material';
import Grid from '@mui/material/Grid';
import { useEffect, useState } from 'react';

import ComplexStatisticsCard from '@/components/cards/Card';
import DashboardLayout from '@/components/layout/DashboardLayout';
import DashboardNavbar from '@/components/navbar/DashboardNavbar';
import { RecordObject } from '@/constants/types';
import { getDashboardInfo } from '@/redux/actions/invoiceActionCreator';
import { useAppDispatch, useAppSelector } from '@/redux/store';

const Dashboard = () => {
  const dispatch = useAppDispatch();
  const { leads = [] } = useAppSelector((state) => state.invoice);
  const [search, setSearch] = useState('');

  useEffect(() => {
    dispatch(getDashboardInfo());
  }, []);

  // Filter leads by name (case-insensitive, partial match)
  const filteredLeads = leads.filter((lead: RecordObject) => lead.name.toLowerCase().includes(search.toLowerCase()));

  return (
    <DashboardLayout>
      <DashboardNavbar search={search} onSearchChange={setSearch} />
      <Container disableGutters sx={{ mt: 4, px: 4, maxWidth: 'none !important' }}>
        <Box py={3}>
          <Grid container spacing={3}>
            {filteredLeads.map((lead: RecordObject) => (
              <Grid size={{ xs: 12, sm: 6, md: 4 }} key={lead.id}>
                <Box mb={2}>
                  <ComplexStatisticsCard
                    color="dark"
                    id={lead.id}
                    name={lead.name}
                    mobileNo={lead.mobileNumber}
                    emailAddress={lead.emailAddress}
                    jobDetails={lead.jobs}
                  />
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>
      </Container>
    </DashboardLayout>
  );
};

export default Dashboard;
