import {
  AUTHENTICATE_USER,
  REGISTER_USER,
  REFRESH_AUTH_TOKEN,
  CHANGE_PASSWORD,
  GET_PRODUCTS_NAMES,
  GENERATE_JOB_REPORT,
  GET_ALL_ITEM_FOR_JOB,
  INSERT_UPDATE_ITEM,
  DELETE_ITEM,
  GET_JOB_BY_ID,
  GET_LEAD_BY_ID,
  INSERT_UPDATE_JOB,
  DELETE_JOB,
  GET_ALL_LEADS,
  INSERT_UPDATE_LEAD,
  DELETE_LEAD,
  RESET_INVOICE_STORE,
  GET_DASHBOARD_INFO,
  GENERATE_PDF,
  SEARCH_PRODUCTS,
  SEARCH_PRODUCT_RATE,
  UPDATE_PRODUCT_RATE,
} from '@/constants/constants';
import { InvoiceAction } from '@/constants/types';

export const authenticateUser = (payload: object): InvoiceAction => ({
  type: AUTHENTICATE_USER,
  payload,
});

export const setWfId = (payload: string): InvoiceAction => ({
  type: REGISTER_USER,
  payload,
});

export const resetInvoiceStore = (): InvoiceAction => ({
  type: RESET_INVOICE_STORE,
});

export const getDashboardInfo = (): InvoiceAction => ({
  type: GET_DASHBOARD_INFO,
});

export const generatePDF = (payload: string): InvoiceAction => ({
  type: GENERATE_PDF,
  payload,
});

export const insertOrUpdateLead = (payload: object): InvoiceAction => ({
  type: INSERT_UPDATE_LEAD,
  payload,
});

export const getJobData = (payload: string): InvoiceAction => ({
  type: GET_JOB_BY_ID,
  payload,
});

export const getLeadData = (payload: string): InvoiceAction => ({
  type: GET_LEAD_BY_ID,
  payload,
});

export const searchProducts = (payload: string): InvoiceAction => ({
  type: SEARCH_PRODUCTS,
  payload,
});

export const searchProductRate = (payload: string): InvoiceAction => ({
  type: SEARCH_PRODUCT_RATE,
  payload,
});

export const updateProductRate = (payload: object): InvoiceAction => ({
  type: UPDATE_PRODUCT_RATE,
  payload,
});
