import AddBoxRoundedIcon from '@mui/icons-material/AddBoxRounded';
import ExpandLessRoundedIcon from '@mui/icons-material/ExpandLessRounded';
import ExpandMoreRoundedIcon from '@mui/icons-material/ExpandMoreRounded';
import { Avatar, IconButton, useTheme } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import { useMemo, useState } from 'react';

import Table from '@/components/table/Table';
import { NAVIGATE_TO } from '@/constants/constants';
import { ColumnData, RecordObject } from '@/constants/types';
import { useAppDispatch } from '@/redux/store';

interface Props {
  id: number;
  color?: 'light' | 'dark';
  name: string;
  mobileNo: string;
  emailAddress?: string;
  jobDetails: RecordObject[];
}

const gradients = [
  'linear-gradient(195deg, #42424a, #191919)',
  'linear-gradient(135deg, #1e3c72, #2a5298)',
  'linear-gradient(160deg, #ff6f00, #ff8f00)',
  'linear-gradient(180deg, #e53935, #e35d5b)',
  'linear-gradient(145deg, #667eea, #764ba2)',
  'linear-gradient(135deg, #00c6ff, #0072ff)',
  'linear-gradient(160deg, #f953c6, #b91d73)',
  'linear-gradient(120deg, #f12711, #f5af19)',
  'linear-gradient(130deg, #43cea2, #185a9d)',
  'linear-gradient(180deg, #ff512f, #dd2476)',
  'linear-gradient(140deg, #1d4350, #a43931)',
  'linear-gradient(135deg, #373b44, #4286f4)',
  'linear-gradient(150deg, #614385, #516395)',
  'linear-gradient(120deg, #0f2027, #203a43, #2c5364)',
  'linear-gradient(120deg, #3a1c71, #d76d77, #ffaf7b)',
];

const ComplexStatisticsCard: React.FC<Props> = ({ id, color = 'light', name, mobileNo, jobDetails }) => {
  const theme = useTheme();
  const dispatch = useAppDispatch();

  const [expanded, setExpanded] = useState(false);
  const handleExpandClick = () => setExpanded(!expanded);

  const randomGradient = useMemo(() => {
    return gradients[Math.floor(Math.random() * gradients.length)];
  }, []);
  const redirect = (url: string) => dispatch({ type: NAVIGATE_TO, payload: { url } });
  const nameInitial = name.trim().charAt(0).toUpperCase();
  const columns: ColumnData[] = [
    {
      width: 100,
      label: 'Site Name',
      dataKey: 'name',
    },
    {
      width: 100,
      label: 'Site Address',
      dataKey: 'address',
    },
    {
      width: 120,
      label: 'Payment Status',
      dataKey: 'paymentStatus',
    },
    {
      width: 100,
      label: 'Actions',
      dataKey: 'actions',
    },
  ];
  return (
    <Card sx={{ overflow: 'visible' }}>
      <Box display="flex" justifyContent="space-between" pt={1} px={2}>
        <Box
          color={color === 'light' ? 'dark' : 'white'}
          display="flex"
          justifyContent="center"
          alignItems="center"
          width="4rem"
          height="4rem"
          mt={-3}
          sx={{
            background: randomGradient,
            color: '#ffffff',
            borderRadius: '0.75rem',
            boxShadow: `0rem 0.25rem 1.25rem 0rem rgba(0, 0, 0, 0.14),
                        0rem 0.4375rem 0.625rem -0.3125rem rgba(64, 64, 64, 0.4)`,
          }}
        >
          <Avatar
            sx={{
              width: 40,
              height: 40,
              borderRadius: 1,
              fontWeight: 'bold',
              fontSize: 35,
              backgroundColor: 'inherit',
            }}
          >
            {nameInitial}
          </Avatar>
        </Box>
        <Box textAlign="right" lineHeight={1.25}>
          <Typography variant="h4">{name}</Typography>
          <Typography variant="button" fontWeight="light" color="text">
            {mobileNo}
          </Typography>
        </Box>
      </Box>
      <Divider sx={theme.customStyles.customDivider} />
      <Box display="flex" justifyContent="space-between" pb={2} px={2}>
        <Typography
          component="p"
          variant="button"
          color="text"
          display="flex"
          sx={{ cursor: 'pointer' }}
          onClick={handleExpandClick}
        >
          Show Job Details
        </Typography>
        <Box textAlign="right" lineHeight={1.25} sx={{ cursor: 'pointer' }}>
          <IconButton
            aria-label="Create New Job"
            sx={{ py: 0, verticalAlign: 'top' }}
            onClick={() => redirect(`/invoice-details?leadId=${id}`)}
          >
            <AddBoxRoundedIcon />
          </IconButton>
          {expanded && <ExpandLessRoundedIcon onClick={handleExpandClick}></ExpandLessRoundedIcon>}
          {!expanded && <ExpandMoreRoundedIcon onClick={handleExpandClick}></ExpandMoreRoundedIcon>}
        </Box>
      </Box>
      {expanded && <Table rows={jobDetails} columns={columns}></Table>}
    </Card>
  );
};

export default ComplexStatisticsCard;
