import { Box } from '@mui/material';

interface Props {
  background: string;
  children: React.ReactNode;
}

const PageLayout: React.FC<Props> = ({ background, children }) => {
  return (
    <Box width="100vw" height="100%" minHeight="100vh" sx={{ overflowX: 'hidden', bgColor: background }}>
      {children}
    </Box>
  );
};

export default PageLayout;
