import { AxiosError, AxiosResponse } from 'axios';
import { Dispatch } from 'react';
import { MiddlewareAPI } from 'redux';

import { NAVIGATE_TO } from '@/constants/constants';
import { AnyAction, ApiConfigType, RecordObject } from '@/constants/types';
import invoiceAPIConfig from '@/redux/actions/invoiceApiConfig';
import { setAccessToken, setRefreshToken } from '@/redux/auth/auth.slice';
import HttpClient from '@/services/HttpClient';

const httpClient = new HttpClient();
const mergedAPIConfig = {
  ...invoiceAPIConfig,
};

const authStore = (store: {
  getState: () => any;
  dispatch: Dispatch<AnyAction>;
}): {
  accessToken: () => string;
  refreshToken: () => string;
  setTokens: (accessToken: string, refreshToken: string) => void;
} => {
  return {
    accessToken: (): string => store.getState()['auth'].token,
    refreshToken: (): string => store.getState()['auth'].refreshToken,

    setTokens: (accessToken, refreshToken): void => {
      store.dispatch(setAccessToken({ token: accessToken }));
      store.dispatch(setRefreshToken({ refreshToken }));
    },
  };
};

type CustomAction = AnyAction & { response?: AxiosResponse | AxiosError };

export const apiMiddleware =
  (store: MiddlewareAPI) =>
  (next: Dispatch<CustomAction>) =>
  async (action: CustomAction): Promise<void> => {
    httpClient.setAuthStore(authStore(store));
    const apiConfig: ApiConfigType | undefined = mergedAPIConfig[action.type];
    if (apiConfig) {
      next(action);
      const reqPayload = apiConfig.buildRequest(action, store);
      const method = reqPayload.method || 'GET';
      const rootUrl = 'https://molly-talented-molly.ngrok-free.app/bsc';
      const url = rootUrl + (reqPayload.path || '');

      try {
        const urlWithParams = appendParams(url, reqPayload?.params || null);
        // console.log("Invoking api:" + urlWithParams);
        let res;
        const responseType = reqPayload?.responseType;
        if (method === 'POST') {
          const body = reqPayload.body;
          if (reqPayload.isFormData) {
            res = await httpClient.formPost(urlWithParams, body);
          } else {
            res = await httpClient.post(urlWithParams, body, responseType);
          }
        } else if (method === 'PUT') {
          if (reqPayload?.isFormData) {
            res = await httpClient.formPut(urlWithParams, reqPayload?.body);
          } else {
            res = await httpClient.put(urlWithParams, reqPayload, responseType);
          }
        } else if (method === 'HEAD') {
          res = await httpClient.get(urlWithParams);
        } else if (method === 'GET' && responseType === 'blob') {
          res = await httpClient.getAsBlob(urlWithParams);
        } else {
          res = await httpClient.getAsJson(urlWithParams);
        }

        if (apiConfig.successHandler) {
          action.response = res;
          const actionsTodispatch = apiConfig.successHandler(res, action, store);
          dispatchAction(store, await actionsTodispatch);
        }
        if (action.callback) {
          action.callback.resolve(res);
        }
      } catch (e: unknown) {
        console.log('Failed to invoke api:' + url);
        const data = e instanceof AxiosError && e.response?.data ? e.response.data : {};
        const _status: RecordObject = {
          errCode: 'Error',
          message: action?.response?.toString(),
          traceId: data?._trace?.traceparent,
        };
        if (e instanceof Error) {
          _status.errCode = e.name;
          _status.message = e.message;

          if (e instanceof AxiosError) {
            if (e.code === 'ECONNABORTED') {
              _status.errCode = 'ERR_TIMEOUT';
              _status.message = 'AxiosError: Timeout Error';
            } else if (!e.response) {
              _status.errCode = 'ERR_NETWORK';
              _status.message = 'AxiosError: Network Error';
            }
          }
        }

        if (apiConfig.failureHandler) {
          action.response = e as AxiosError;
          const actionsTodispatch = apiConfig.failureHandler(action, store);
          dispatchAction(store, actionsTodispatch);
        }
        if (action.callback) {
          action.callback.reject(e);
        }
        const axiosError = e as AxiosError;
        const resCode = axiosError?.response?.status;
        if (resCode === 401) {
          console.log('Logged out');
          store.dispatch({ type: NAVIGATE_TO, payload: { url: '/login' } });
        }
      }
    } else next(action);
  };

const appendParams = (url: string, params: RecordObject | null): string => {
  if (params) {
    let i = 0;
    for (const key in params) {
      if (params[key]) {
        url += (i === 0 ? '?' : '&') + key + '=' + params[key];
        i++;
      }
    }
  }
  return url;
};

const dispatchAction = (store: MiddlewareAPI, actionsToDispatch: AnyAction[] | void): void => {
  if (actionsToDispatch) {
    actionsToDispatch.forEach((actionToDispatch) => {
      store.dispatch(actionToDispatch);
    });
  }
};
