import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import { Box, TextField, Typography, Card, IconButton, Grid, Button, Autocomplete } from '@mui/material';
import { useEffect } from 'react';

import { Product } from '@/constants/types';
import { searchProducts } from '@/redux/actions/invoiceActionCreator';
import { useAppDispatch, useAppSelector } from '@/redux/store';

interface ProductFormProps {
  index: number;
  total: number;
  onDelete: (index: number) => void;
  onProductChange: (index: number, updates: Partial<Product>, source?: boolean) => void;
  handleProductBlur: (
    index: number,
    field: 'total' | 'id' | 'name' | 'description' | 'quantity' | 'rate' | 'status',
  ) => void;
  addProduct: () => void;
  product: {
    name: string;
    description: string;
    quantity: string;
    rate: string;
    total: number;
  };
  errors: {
    name: string;
    description: string;
    quantity: string;
    rate: string;
    total: string;
  };
  touched: {
    name: boolean;
    description: boolean;
    quantity: boolean;
    rate: boolean;
    total: boolean;
  };
}

const ProductForm: React.FC<ProductFormProps> = ({
  index,
  total,
  onDelete,
  onProductChange,
  handleProductBlur,
  addProduct,
  product,
  errors,
  touched,
}) => {
  const dispatch = useAppDispatch();
  const { products = [], productRates = {} } = useAppSelector((state) => state.invoice);

  const calculateTotal = (quantity: number, rate: number) => {
    return quantity * rate;
  };

  const handleQuantityChange = (value: string) => {
    const quantity = parseFloat(value) || 0;
    const rate = parseFloat(product.rate) || 0;
    const total = calculateTotal(quantity, rate);

    onProductChange(index, {
      quantity: value,
      total: total,
    });
  };

  const handleRateChange = (value: string, getOrUpdateProduct = false) => {
    const rate = parseFloat(value) || 0;
    const quantity = parseFloat(product.quantity) || 0;
    const total = calculateTotal(quantity, rate);

    onProductChange(
      index,
      {
        rate: value,
        total: total,
      },
      getOrUpdateProduct,
    );
  };

  const handleProductName = (value: string) => {
    dispatch(searchProducts(value));
  };

  useEffect(() => {
    if (product.name && !product.rate && productRates[product.name]) {
      handleRateChange(productRates[product.name], false);
    }
  }, [productRates]);

  return (
    <Card sx={{ p: 3, mb: 2 }}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
        <Typography variant="h6">Product {index + 1}</Typography>
        {total > 1 && (
          <IconButton onClick={() => onDelete(index)} color="error" size="small">
            <DeleteIcon />
          </IconButton>
        )}
      </Box>

      <Grid container spacing={3}>
        <Grid size={{ xs: 12, md: 6 }}>
          <Autocomplete
            disablePortal
            options={products.map((p) => p.name)}
            inputValue={product.name}
            value={product.name}
            onInputChange={(event, newInputValue) => {
              handleProductName(newInputValue);
              onProductChange(index, { name: newInputValue });
            }}
            onChange={(event, newInputValue) => {
              if (newInputValue) {
                onProductChange(index, { name: newInputValue }, true);
              }
            }}
            onBlur={() => handleProductBlur(index, 'name')}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Product Name"
                required
                error={Boolean(touched.name && errors.name)}
                helperText={touched.name && errors.name}
              />
            )}
          />
        </Grid>

        <Grid size={{ xs: 12, sm: 6, md: 2 }}>
          <TextField
            label="Quantity"
            variant="outlined"
            fullWidth
            required
            value={product.quantity}
            onChange={(e) => handleQuantityChange(e.target.value)}
            onBlur={() => handleProductBlur(index, 'quantity')}
            error={Boolean(touched.quantity && errors.quantity)}
            helperText={touched.quantity && errors.quantity}
          />
        </Grid>

        <Grid size={{ xs: 12, sm: 6, md: 2 }}>
          <TextField
            label="Rate"
            variant="outlined"
            fullWidth
            required
            value={product.rate}
            onChange={(e) => handleRateChange(e.target.value, true)}
            onBlur={() => handleProductBlur(index, 'rate')}
            error={Boolean(touched.rate && errors.rate)}
            helperText={touched.rate && errors.rate}
          />
        </Grid>

        <Grid size={{ xs: 12, md: 2 }}>
          <TextField
            label="Total"
            variant="outlined"
            fullWidth
            disabled
            value={product.total.toFixed(2)}
            slotProps={{
              input: {
                readOnly: true,
              },
            }}
          />
        </Grid>
        {index == total - 1 && (
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={addProduct}
            sx={{ minWidth: 'auto', ml: 'auto' }}
          >
            Add Item
          </Button>
        )}
      </Grid>
    </Card>
  );
};

export default ProductForm;
