import { Action, Dispatch, MiddlewareAPI } from 'redux';

import { NAVIGATE_TO } from '@/constants/constants';
import { RecordObject } from '@/constants/types';
import { getNavigate } from '@/redux/store';

const getPath = (action: RecordObject): string => action?.payload?.url;

export const navigationMiddleware =
  (api: MiddlewareAPI) =>
  (next: Dispatch) =>
  (action: Action): Action => {
    if (action.type === NAVIGATE_TO) {
      const nextUrl = getPath(action);
      const navigate = getNavigate();
      window.scrollTo(0, 0);
      navigate(nextUrl);
    }
    const result = next(action);
    return result;
  };
