import { Box, Button, Checkbox, TextField, Typography } from '@mui/material';
import Card from '@mui/material/Card';
import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';

import bgImage from '@/assets/images/bg-sign-in-basic.jpeg';
import BasicLayout from '@/components/layout/BasicLayout';
import { NAVIGATE_TO } from '@/constants/constants';
import { authenticateUser } from '@/redux/actions/invoiceActionCreator';

const SignUp = () => {
  const dispatch = useDispatch();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
  });

  const [errors, setErrors] = useState({
    name: '',
    email: '',
    password: '',
  });

  const [touched, setTouched] = useState({
    name: false,
    email: false,
    password: false,
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { id, value } = e.target;
    setFormData((prev) => ({ ...prev, [id]: value }));
    setErrors((prev) => ({ ...prev, [id]: '' }));
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const { id } = e.target;
    setTouched((prev) => ({ ...prev, [id]: true }));
  };

  useEffect(() => {
    validateForm();
  }, [touched]);

  const validateForm = () => {
    let valid = true;
    const newErrors = { name: '', email: '', password: '' };

    if (!formData.name.trim()) {
      newErrors.name = 'Name is required';
      valid = false;
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
      valid = false;
    } else if (!/^\S+@\S+\.\S+$/.test(formData.email)) {
      newErrors.email = 'Invalid email format';
      valid = false;
    }

    if (!formData.password) {
      newErrors.password = 'Password is required';
      valid = false;
    } else if (formData.password.length < 6) {
      newErrors.password = 'Password must be at least 6 characters';
      valid = false;
    }

    setErrors(newErrors);
    return valid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (validateForm()) {
      console.log('Submitting:', formData);
      authenticateUser(formData);
    }
  };

  const redirect = (url: string) => dispatch({ type: NAVIGATE_TO, payload: { url } });

  return (
    <BasicLayout image={bgImage}>
      <Card
        sx={{
          overflow: 'visible',
          position: 'relative',
          display: 'flex',
          flexDirection: 'column',
          borderRadius: '0.75rem',
        }}
      >
        <Box
          mx={2}
          mt={-3}
          p={2}
          mb={1}
          textAlign="center"
          sx={{
            background: 'linear-gradient(175deg, #49a3f1, #1A73E8)',
            color: '#344767',
            borderRadius: '0.5rem',
            boxShadow:
              '0rem 0.25rem 1.25rem 0rem rgba(0, 0, 0, 0.14), 0rem 0.4375rem 0.625rem -0.3125rem rgba(0, 187, 212, 0.4)',
          }}
        >
          <Typography variant="h5" fontWeight="medium" color="white" mt={1}>
            Sign Up
          </Typography>
        </Box>
        <Box pt={4} pb={3} px={3}>
          <Box component="form" role="form" onSubmit={handleSubmit}>
            <Box mb={2}>
              <TextField
                id="name"
                type="text"
                label="Name"
                variant="outlined"
                fullWidth
                value={formData.name}
                onChange={handleChange}
                onBlur={handleBlur}
                error={Boolean(touched.name && errors.name)}
                helperText={touched.name && errors.name}
              />
            </Box>
            <Box mb={2}>
              <TextField
                id="email"
                label="Email"
                variant="outlined"
                fullWidth
                value={formData.email}
                onChange={handleChange}
                onBlur={handleBlur}
                error={Boolean(touched.email && errors.email)}
                helperText={touched.email && errors.email}
              />
            </Box>
            <Box mb={2}>
              <TextField
                id="password"
                type="password"
                label="Password"
                variant="outlined"
                fullWidth
                value={formData.password}
                onChange={handleChange}
                onBlur={handleBlur}
                error={Boolean(touched.password && errors.password)}
                helperText={touched.password && errors.password}
              />
            </Box>
            <Box mt={1} mb={1}>
              <Button
                type="submit"
                color="info"
                fullWidth
                sx={{
                  background: 'linear-gradient(175deg, #49a3f1, #1A73E8)',
                  color: '#ffffff',
                  borderRadius: '0.5rem',
                  boxShadow:
                    '0rem 0.25rem 1.25rem 0rem rgba(0, 0, 0, 0.14), 0rem 0.4375rem 0.625rem -0.3125rem rgba(0, 187, 212, 0.4)',
                }}
              >
                Sign Up
              </Button>
            </Box>
            <Box mt={3} mb={1} textAlign="center">
              <Typography variant="button" color="text">
                Already have an account?{' '}
                <Typography
                  variant="button"
                  color="info"
                  fontWeight="medium"
                  sx={{ cursor: 'pointer' }}
                  onClick={() => redirect('/login')}
                >
                  Sign In
                </Typography>
              </Typography>
            </Box>
          </Box>
        </Box>
      </Card>
    </BasicLayout>
  );
};

export default SignUp;
