import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export type AuthState = {
  token: string;
  user?: string;
  refreshToken: string;
};

const initialState: AuthState = {
  token: '',
  refreshToken: '',
};

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setCredentials: (state, action: PayloadAction<{ token: string; user: string }>) => {
      state.token = action.payload.token;
      state.user = action.payload.user;
    },
    setAccessToken: (state, action: PayloadAction<{ token: string }>) => {
      state.token = action.payload.token;
    },
    setRefreshToken: (state, action: PayloadAction<{ refreshToken: string }>) => {
      state.refreshToken = action.payload.refreshToken;
    },
    logOut: (state) => {
      state.token = '';
      state.user = undefined;
    },
  },
});

export const { setCredentials, logOut, setAccessToken, setRefreshToken } = authSlice.actions;
export default authSlice.reducer;
