import { Box } from '@mui/material';
import Grid from '@mui/material/Grid';

import PageLayout from '@/components/layout/PageLayout';

interface Props {
  image?: string;
  children: React.ReactNode;
}

const BasicLayout: React.FC<Props> = ({ image, children }) => {
  const gradientStart = 'rgba(52, 71, 103, 0.6)';
  const gradientEnd = 'rgba(33, 33, 33, 0.6)';

  return (
    <PageLayout background="default">
      <Box
        position="absolute"
        width="100%"
        minHeight="100vh"
        sx={{
          backgroundImage: image ? `linear-gradient(${gradientStart}, ${gradientEnd}), url(${image})` : 'none',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          backgroundRepeat: 'no-repeat',
        }}
      />
      <Box px={1} width="100%" height="100vh" mx="auto">
        <Grid container spacing={1} justifyContent="center" alignItems="center" height="100%">
          <Grid size={{ xs: 11, sm: 9, md: 5, lg: 4, xl: 3 }}>{children}</Grid>
        </Grid>
      </Box>
    </PageLayout>
  );
};

export default BasicLayout;
